<template>
  <div class="reset-password-success">
    <div class="reset-password-success__checked-icon">
      <Icon name="check" />
    </div>
    <Text variant="p" size="14px" line-height="16px" mt="20px" mb="82px">
      {{ tp('resetSuccess', { email: 'ab****ef@gmail.com' }) }}
    </Text>
    <Button class="reset-password-success__action" @click="goToLogin">
      {{ tp('returnToLogin') }}
    </Button>
  </div>
</template>

<script>
import { ROUTES_NAMES } from '@/admin/common/routes'
import { useI18n } from '@/shared/plugins/i18n'
import { useRouter } from '@/shared/plugins/vue-router'
import Icon from '@/shared/components/Icon/Icon'
import Text from '@/shared/components/UIKit/Text/Text'
import Button from '@/shared/components/UIKit/Button'

export default {
  name: 'ResetPasswordSuccess',
  components: { Button, Text, Icon },
  setup () {
    const router = useRouter()
    const { tp } = useI18n('adminAuth.resetPassword')

    const goToLogin = () => {
      router.push({
        name: ROUTES_NAMES.LOGIN
      })
    }

    return {
      tp,
      goToLogin
    }
  }
}
</script>

